import { CButton, CCard, CCardBody, CCol, CFormInput, CFormSelect, CRow } from '@coreui/react'
import axios from 'axios'
import { ref } from 'firebase/storage'
import React, { useEffect, useState } from 'react'
import { storage } from 'src/firebase'
const url = 'https://yog-seven.vercel.app'
const url2 = 'https://yog-seven.vercel.app'

const SubmitAppointment = ({ id }) => {
    const [appointment, setAppointment] = useState(false)
    const [action1, setAction1] = useState(false)
    const [AppointmentDate, setAppointmentDate] = useState('')
    const [AppointmentTime, setAppointmentTime] = useState('')
    const [name, setName] = useState('')
    const [contact, setContact] = useState('')
    const [StaffName, setStaffName] = useState('')


    let user = JSON.parse(localStorage.getItem('user-info'))
    const token = user.token;
    const username = user.user.username;
    const [result1, setResult1] = useState([]);
    const [paging, setPaging] = useState(0)
    const headers = {
        'Authorization': `Bearer ${token}`,
        'My-Custom-Header': 'foobar'
    };
    useEffect(() => {
        getAppointment()
        getStaff()
        searchSingle(id)
    }, []);
    function searchSingle(id) {
        const headers = {
            'Authorization': `Bearer ${token}`,
            'My-Custom-Header': 'foobar'
        };
        axios.get(`${url}/memberForm/${id}`, { headers },
        ).then((resp) => {
            console.log(resp.data);
            setName(resp.data.Fullname)
            setContact(resp.data.ContactNumber)
        }).catch((err) => {
            console.log(err);
        })
    }
    const [staff, setStaff] = useState([])
    function getStaff() {
        axios.get(`${url2}/employeeForm/all`, {
            headers: {
                'Authorization': `Bearer ${token}`
            }
        })
            .then((res) => {
                setStaff(res.data)
            })
            .catch((error) => {
                console.error(error)
            })
    }

    function getAppointment() {
        axios.get(`${url}/AppointmentClient/all`, {
            headers: {
                'Authorization': `Bearer ${token}`
            }
        })
            .then((res) => {
                console.log(res.data)
                setResult1(res.data.reverse())
            })
            .catch((error) => {
                console.error(error)
            })
    }


    function createGallery() {
        const data = {
            userId: id,
            ClientName: name,
            ClientNumber: contact,
            AppointmentId: ("AP" + username + result1.length),
            AppointmentDate: AppointmentDate,
            AppointmentTime: AppointmentTime,
            StaffName: StaffName,
        }
        axios.post(`${url}/AppointmentClient/create`, data, { headers })
            .then((resp) => {
                console.log(resp.data)
                alert('Successfully Added')
                getAppointment()
                setAction1(false)
            })
            .catch((error) => console.log(error))
    }

    return (
        <div>
            <CCard>
                <CCardBody>
                    <CRow className='mt-3'>
                        <CCol lg={6} md={6} sm={12}>
                            <CFormInput
                                className="mb-1 mr-3"
                                type="text"
                                label="Client Name"
                                placeholder='Enter Client Name'
                                value={name}
                                onChange={(e) => setName(e.target.value)}
                            />
                        </CCol>
                        <CCol lg={6} md={6} sm={12}>
                            <CFormInput
                                className="mb-1 mr-3"
                                type="number"
                                label="Client Number"
                                placeholder='Enter Client Number'
                                value={contact}
                                onChange={(e) => setContact(e.target.value)}
                            />
                        </CCol>
                        <CCol lg={4} md={4} sm={12}>
                            <CFormSelect
                                label="Staff Name"
                                value={StaffName}
                                onChange={(e) => setStaffName(e.target.value)}
                            >
                                <option >Select</option>
                                {staff.filter((list) => list.username === username && list.selected === 'Select').map((item, index) => (
                                    item.username === username && (
                                        <option key={index}>{item.FullName}</option>
                                    )
                                ))}
                            </CFormSelect>
                        </CCol>
                        <CCol lg={4} md={4} sm={12}>
                            <CFormInput
                                className="mb-1 mr-3"
                                type="date"
                                label="Appointment Date"
                                value={AppointmentDate}
                                onChange={(e) => setAppointmentDate(e.target.value)}
                            />
                        </CCol>
                        <CCol lg={4} md={4} sm={12}>
                            <CFormInput
                                className="mb-1 mr-3"
                                type="time"
                                label="Appointment Time"
                                value={AppointmentTime}
                                onChange={(e) => setAppointmentTime(e.target.value)}
                            />
                        </CCol>
                        <CCol className="mt-2" lg={6} md={6} sm={12}>
                            <CButton className="mt-2" onClick={() => createGallery()}>Save</CButton>
                        </CCol>
                    </CRow>
                </CCardBody>
            </CCard>
        </div>
    )
}

export default SubmitAppointment
