import { CButton, CFormLabel, CTable, CTableBody, CTableDataCell, CTableHead, CTableHeaderCell, CTableRow } from "@coreui/react";
import moment from "moment";

const Table = ({ data, column }) => {
  return (
    <div className="table-wrapper">
      <CTable className='mt-3' align="middle" bordered style={{ borderColor: "#0B5345" }} hover responsive>
        <CTableHead style={{ backgroundColor: "#0B5345", color: "white" }}>
          <CTableRow>
            {column.map((item) => (
              <TableHeadItem item={item} />
            ))}
          </CTableRow>
        </CTableHead>
        <CTableBody>
          {data.map((item, index) => (
            <TableRow item={item} column={column} qu={index} />
          ))}
        </CTableBody>
      </CTable>
    </div>
  );
};

const TableHeadItem = ({ item }) => <CTableHeaderCell>{item.heading}</CTableHeaderCell>;
const TableRow = ({ item, column, qu }) => (
  <CTableRow>
    {column.map((columnItem, index) => {
      return (
        <CTableDataCell key={index}>
          {columnItem.uni !== undefined && qu + 1}
          {columnItem.mId !== undefined && String(item[`${columnItem.mId}`]).slice(-6)}
          {columnItem.link !== undefined && <a href={item[`${columnItem.link}`]} target="_blank">view</a>}
          {columnItem.value !== undefined && item[`${columnItem.value}`]}{" "}
          {columnItem.date !== undefined && moment(item[`${columnItem.date}`]).format("DD-MM-YYYY")}{" "}
          {columnItem.btn !== undefined && (
            <CButton className="btn btn_enquiry">{columnItem.btn}</CButton>
          )}{" "}
          {columnItem.btn1 !== undefined && columnItem.btn1}{" "}
          {columnItem.btn2 !== undefined && columnItem.btn2}{" "}
          {columnItem.btn3 !== undefined && columnItem.btn3}{" "}
          {columnItem.btn4 !== undefined && columnItem.btn4}{" "}
          {columnItem.label !== undefined && (
            <CFormLabel className="label">{item[`${columnItem.label}`]}</CFormLabel>
          )}
        </CTableDataCell>
      );
    })}
  </CTableRow>
);

export default Table;
