import { CCol, CRow, CTable, CTableBody, CTableDataCell, CTableHead, CTableHeaderCell, CTableRow } from "@coreui/react";
import moment from "moment";
import React from "react";

const IndividualAccess = ({ clientData }) => {

  var date2 = new Date(moment(clientData?.startDate).format("MM-DD-YYYY"));
  let today = new Date()
  var date1 = new Date(moment(clientData?.endDate).format("MM-DD-YYYY"));

  function datediff(first, second) {
    return Math.round((second - first) / (1000 * 60 * 60 * 24));
  }

  return (
    <>
      <div className="mt-3 ml-3">Recent Purchased Services</div>
      <div className="dbox-14" style={{ marginBottom: "20px" }}>
        <CTable className='mt-3' align="middle" bordered style={{ borderColor: "#0B5345" }} hover responsive>
          <CTableHead style={{ backgroundColor: "#0B5345", color: "white" }}>
            <CTableRow>
              <CTableHeaderCell>Service Id</CTableHeaderCell>
              <CTableHeaderCell>Service Name</CTableHeaderCell>
              <CTableHeaderCell>Service Duration</CTableHeaderCell>
              <CTableHeaderCell>Total Days</CTableHeaderCell>
              <CTableHeaderCell>Count Down Days</CTableHeaderCell>
              <CTableHeaderCell>Start Date</CTableHeaderCell>
              <CTableHeaderCell>Expiry Date</CTableHeaderCell>
              <CTableHeaderCell>Package Status</CTableHeaderCell>
            </CTableRow>
          </CTableHead>
          <CTableBody>
            <CTableRow>
              <CTableDataCell>{String(clientData?._id).slice(-6)}</CTableDataCell>
              <CTableDataCell>{clientData?.ServiceName}</CTableDataCell>
              <CTableDataCell>{clientData?.duration}</CTableDataCell>
              <CTableDataCell>{datediff(date2, date1)}</CTableDataCell>
              <CTableDataCell>{datediff(today, date1) > 0 ? datediff(today, date1) : 0}</CTableDataCell>
              <CTableDataCell>{moment(clientData?.startDate).format("DD-MM-YYYY")}</CTableDataCell>
              <CTableDataCell>{moment(clientData?.endDate).format("DD-MM-YYYY")}</CTableDataCell>
              <CTableDataCell>{clientData?.status}</CTableDataCell>
            </CTableRow>
          </CTableBody>
        </CTable>
      </div>
    </>
  );
};

export default IndividualAccess;
