
import { useState } from "react";
import { CCard, CCardBody, CCardHeader, CNav, CNavItem, CNavLink, CTabContent, CTabPane } from "@coreui/react";

const Tabs = ({ data, tabI }) => {
  const [tabs, setTabs] = useState(tabI != null ? tabI - 1 : 0);

  return (
    <>
      <CCard>
        <CCardHeader style={{ backgroundColor: '#0b5345' }}>
          <CNav variant="pills" role="tablist" >
            {data.map((item, index) => {
              return (
                <CNavItem>
                  <CNavLink
                    style={{ color: "white" }}
                    href="javascript:void(0);"
                    active={tabs === index}
                    onClick={() => setTabs(index)}
                  >

                    {item.title}
                  </CNavLink>
                </CNavItem>
              );
            })}
          </CNav>
        </CCardHeader>
        <CCardBody>
          <CTabContent>
            {data.map((item, index) => {
              return <CTabPane role="tabpanel" aria-labelledby="home-tab" visible={tabs === index}>{item.compo}</CTabPane>;
            })}
          </CTabContent>
        </CCardBody>
      </CCard>
    </>
  );
};

export default Tabs;
